import React, { useState } from "react";
import "./App.css";
import { db, timestamp } from "./firebase";
import { collection, addDoc } from "firebase/firestore";

function App() {
  const [formData, setFormData] = useState({
    surname: "",
    name: "",
    patronymic: "",
    position: "",
    department: ""
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      //    Firestore
      await addDoc(collection(db, "applications"), {
        ...formData,
        createdAt: timestamp(), //    
        updatedAt: timestamp() //    
      });

      alert("  !");
      setFormData({
        surname: "",
        name: "",
        patronymic: "",
        position: "",
        department: ""
      });
    } catch (error) {
      console.error("   : ", error);
      alert("    .");
    }
  };

  return (
    <div className="App">
      <h1>     </h1>
      <form onSubmit={handleSubmit}>
        <label>
          :
          <input
            type="text"
            name="surname"
            value={formData.surname}
            onChange={handleChange}
            required
          />
        </label>
        <label>
          :
          <input
            type="text"
            name="name"
            value={formData.name}
            onChange={handleChange}
            required
          />
        </label>
        <label>
          :
          <input
            type="text"
            name="patronymic"
            value={formData.patronymic}
            onChange={handleChange}
          />
        </label>
        <label>
          :
          <input
            type="text"
            name="position"
            value={formData.position}
            onChange={handleChange}
            required
          />
        </label>
        <label>
          :
          <input
            type="text"
            name="department"
            value={formData.department}
            onChange={handleChange}
            required
          />
        </label>
        <button type="submit"></button>
      </form>
    </div>
  );
}

export default App;